import React from 'react';

const Main = React.lazy(() => import('./pages/Main/Main'));
const About = React.lazy(() => import('./pages/About/About'));
const Works = React.lazy(() => import('./pages/Works/Works'));
const Detail = React.lazy(() => import('./pages/Works/Detail'));
const Contact = React.lazy(() => import('./pages/Contact/Contact'));
const Test = React.lazy(() => import('./pages/Main/Test'));

const routes = [
  { path: '/', name: 'Main', component: Main, exact: true },
  { path: '/test', name: 'Test', component: Test, exact: true },
  { path: '/about', name: 'About', component: About, exact: true },
  { path: '/works', name: 'Works', component: Works, exact: true },
  { path: '/works/tms', name: 'TMS', component: Detail, exact: true },
  {
    path: '/works/buyerportal',
    name: 'BuyerPortal',
    component: Detail,
    exact: true,
  },
  {
    path: '/works/fulfillment',
    name: 'Fulfillment',
    component: Detail,
    exact: true,
  },
  {
    path: '/works/returnman',
    name: 'Returnman',
    component: Detail,
    exact: true,
  },
  {
    path: '/works/shippingman',
    name: 'Shippingman',
    component: Detail,
    exact: true,
  },
  {
    path: '/works/sweetspotFO',
    name: 'SweetspotFO',
    component: Detail,
    exact: true,
  },
  {
    path: '/works/sweetspotBO',
    name: 'SweetspotBO',
    component: Detail,
    exact: true,
  },
  { path: '/contact', name: 'Contact', component: Contact, exact: true },
];

export default routes;
